<template>
  <div>
    <table class="table table-borderless mx-auto text-center" style="width: 95%">
      <thead>
        <tr class="small-td-2">
          <td class="text-center py-0" colspan="7">
            <i class="text-muted">Less info is shown at smaller resolutions</i>
          </td>
        </tr>
        <tr>
          <th scope="col">Division</th>
          <th scope="col">State</th>
          <th class="text-right" scope="col">Party</th>
          <th scope="col"></th>
          <th class="big-td text-left" scope="col">Opposition</th>
          <th class="small-td text-left" scope="col">Opp.</th>
          <th scope="col">Margin</th>
          <th class="big-td-2" scope="col">Chance</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="i of pendulumDisplay" :key="i.division"
          :style="{ backgroundColor: i.margin <= biggestFlipped + biggestFlippedMargin ? toRGB(colors[i.party], (i.margin + colorOffset) / colorDiv) : toRGB(colors[i.party], (biggestFlipped + biggestFlippedMargin + colorOffset) / colorDiv) }">
            <td><a :href="'https://en.wikipedia.org/wiki/Division_of_' + i.division" target="_blank" v-html="i.flip ? `<b>${i.division}</b>` : i.division"></a></td>
            <td>{{ i.state }}</td>
            <td class="big-td text-right" v-html="i.flip ? `<b>${parties[i.party]}</b>` : parties[i.party]"></td>
            <td class="small-td text-right" v-html="i.flip ? `<b>${i.party}</b>` : i.party"></td>
            <td v-html="`<small>${ i.flip ? '&larr;' : 'vs.' }</small>`"></td>
            <td class="big-td text-left" v-html="parties[i.opposition]"></td>
            <td class="small-td text-left">{{ i.opposition }}</td>
            <td class="big-td"><small>{{ fixRound(i.margin + (i.flip ? i.flip : -i.shift)).toFixed(1) }} &rarr;</small> <span :style="{ fontWeight: i.flip ? 'bold' : 'normal' }">{{ fixRound(i.margin).toFixed(1) }}</span></td>
            <td class="small-td" v-html="i.flip ? `<b>${fixRound(i.margin).toFixed(1)}</b>` : fixRound(i.margin).toFixed(1)"></td>
            <td class="big-td-2">{{ i.confidence == 1 ? '>99' : Math.floor(i.confidence * 100) }}<small>% {{ i.flip ? 'flip' : 'retain' }}</small></td>
        </tr>
        <tr>
          <td class="text-center" colspan="7">
            <button class="btn btn-sm btn-secondary mt-2" v-on:click="toggleView">{{ viewMore ? 'Hide' : 'View' }} Safer Seats</button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<style scoped>
table {
  font-weight: 500; /* default is 400 */
}

th {
  padding-left: 2px !important;
  padding-right: 2px !important;
}

tr {
  border: 2px solid white;
}

td {
  padding: 2px !important;
}

td a {
  color: black;
}

@media (min-width: 1200px) {
  th {
    padding-top: 0 !important;
  }
}

@media (min-width: 576px) {
  .small-td {
    display: none;
  }
}

@media (max-width: 575px) {
  .big-td {
    display: none;
  }
}

@media (min-width: 768px) {
  .small-td-2 {
    display: none;
  }
}

@media (max-width: 767px) {
  .big-td-2 {
    display: none;
  }
}
</style>

<script>
import * as p from '../../pendulum';

export default {
  name: 'Pendulum',
  props: {
    colors: Object
  },

  methods: {
    toRGB(hex, alpha = 1) {
      const [r, g, b] = hex.match(/\w\w/g).map(x => parseInt(x, 16));
      return `rgba(${r},${g},${b},${alpha})`;
    },
    fixRound(x) {
      return Math.round((x + Number.EPSILON) * 10) / 10
    },
    toggleView() {
      this.pendulumDisplay = this.viewMore ? this.pendulum.filter(x => x.margin < this.biggestFlipped + this.biggestFlippedMargin) : this.pendulum
      this.viewMore = !(this.viewMore)
    }
  },

  data() {
    return {
      pendulum: p,
      pendulumDisplay: [],
      viewMore: false,
      biggestFlipped: 0,
      biggestFlippedMargin: 0,
      colorDiv: 7.75,
      colorOffset: 0.75,
      parties: {
        'LP': 'Liberal',
        'NP': 'National',
        'LNP': 'LNP <small>QLD</small>',
        'ALP': 'Labor',
        'GRN': 'Greens',
        'KAP': 'Katter\'s',
        'CA': 'Centre All.',
        'IND': 'Independent',
        'ON': 'One Nation',
        'CLP': 'CLP <small>NT</small>'
      }
    }
  },

  created() {
    this.pendulum = p['ALP'].concat(p['LNC'])
    this.pendulum.sort((a, b) => a.margin - b.margin)
    // let flipped = this.pendulum.filter(x => x.flip)
    this.biggestFlipped = this.pendulum.find(x => x.confidence == 1).margin
    this.pendulumDisplay = this.pendulum.filter(x => x.margin < this.biggestFlipped + this.biggestFlippedMargin)
  }
}
</script>